import { Component, OnInit, ViewChild } from '@angular/core';
import { faStopwatch } from '@fortawesome/free-solid-svg-icons';
import {
  NgbModal,
  ModalDismissReasons,
  NgbModalOptions,
} from '@ng-bootstrap/ng-bootstrap';
import { AuthService } from '../services/auth.service';
import { HttpService } from '../services/http.service';
import { Router, ActivatedRoute } from '@angular/router';
import { NgForm } from '@angular/forms';
import { environment } from '../../environments/environment';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss'],
})
export class LoginComponent implements OnInit {
  toggleBtn = {
    msgText: 'Don\'t have an account?',
    linkText: 'Sign Up',
    headerText: 'Account Login',
  };
  signInFormEnabled = true;
  timeLeft = 10;
  timerText: string;
  timeTracker: any;
  otpLength: number;
  timerExpired = false;
  timerView = false;
  faStopwatch = faStopwatch;
  otpView = false;
  closeResult = '';
  forgotPwdModalRef: any;
  signUpModalRef: any;
  @ViewChild('signUpForm', { static: false }) signUpForm: NgForm;
  @ViewChild('signInForm', { static: false }) signInForm: NgForm;

  constructor(
    private modalService: NgbModal,
    private authService: AuthService,
    private _httpService: HttpService,
    private router: Router,
    private activatedRoute: ActivatedRoute
  ) { }

  ngOnInit(): void { }

  launchModal(ref: any, name: string) {
    const ngbModalOptions: NgbModalOptions = {
      backdrop: 'static',
      keyboard: false,
    };
    if (name === 'SignUp') {
      this.sendOTP();
      console.log(this.signUpForm.form.value);
    }
    this.modalService.open(ref, ngbModalOptions).result.then(
      (result) => {
        this.closeResult = `Closed with: ${result}`;
      },
      (reason) => {
        this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
        this.otpView = false;
      }
    );
  }

  private getDismissReason(reason: any): string {
    if (reason === ModalDismissReasons.ESC) {
      return 'by pressing ESC';
    } else if (reason === ModalDismissReasons.BACKDROP_CLICK) {
      return 'by clicking on a backdrop';
    } else {
      return `with: ${reason}`;
    }
  }

  toggleBtnHandler() {
    this.signInFormEnabled = !this.signInFormEnabled;
    if (this.signInFormEnabled) {
      this.toggleBtn = {
        msgText: 'Don\'t have an account?',
        linkText: 'Sign Up',
        headerText: 'Account Login',
      };
    } else {
      this.toggleBtn = {
        msgText: 'Existing User?',
        linkText: 'Sign In',
        headerText: 'Create Your Account',
      };
    }
  }

  onOtpChange($event) {
    console.log($event);
    this.otpLength = $event;
  }

  countdown() {
    if (this.timeLeft === 0) {
      clearTimeout(this.timeTracker);
      this.timeLeft = 10;
      this.timerText =
        'OTP entered is expired. Please generate a new one and try again';
      this.timerExpired = true;
    } else {
      this.timerView = true;
      this.timerExpired = false;
      this.timerText = 'OTP expires within ' + this.timeLeft + ' seconds';
      this.timeLeft--;
    }
  }

  sendOTP() {
    console.log('Sending OTP');
    clearTimeout(this.timeTracker);
    this.timeLeft = 10;
    this.timerText = 'OTP expires within ' + this.timeLeft + ' seconds';
    this.timerView = false;
    this.timeTracker = setInterval(() => {
      this.countdown();
    }, 1000);
  }

  enablePasswordInput() {
    this.sendOTP();
    this.otpView = true;
  }

  login() {
    let formValues = this.signInForm.form.value;
    let body = {
      mobile_number: formValues.mobileNumber,
      password: formValues.password
    }
    this.authService.login(environment.LOGIN, JSON.stringify(body)).subscribe((res: any) => {
        let body = res.body;
        this.authService.token = body.token;
        delete body.token;
        this.authService.usrObj = body;
        console.log(body);
        this.authService.clientName = body.firstname.charAt(0).toUpperCase() + body.firstname.slice(1) + ' ' + body.lastname.charAt(0).toUpperCase() + body.lastname.slice(1);
        this.authService.isLoggedIn = true;
        this.router.navigate(['/profile'], { relativeTo: this.activatedRoute });
      }, (error: Error) => {
    });
  }
}
